export default {
	data() {
		return {
			content:[],
			translate_show:false,
			page:this.$store.state.Word.page
		};
	},
	onLoad() {
		if(this.$store.state.Word.which_dict==''){
			uni.showModal({
			    title: '提示',
			    content: '请先选择单词种类',
			    success: function (res) {
			        if (res.confirm) {
			            // console.log('用户点击确定');
						uni.navigateTo({
							url: '/pages/word_list_select/word_list_select'
						});
			        } else if (res.cancel) {
			            // console.log('用户点击取消');
						uni.switchTab({
						    url: '/pages/word_list_index/word_list_index'
						});
			        }
			    }
			});
			
		}else {
			if(this.$store.state.Word.read_model==''||this.$store.state.Word.read_order==''){
				uni.showModal({
				    title: '提示',
				    content: '请先进行单词设置',
				    success: function (res) {
				        if (res.confirm) {
				            // console.log('用户点击确定');
							uni.navigateTo({
								url: '/pages/word_list_setting/word_list_setting'
							});
				        } else if (res.cancel) {
				            // console.log('用户点击取消');
							uni.switchTab({
							    url: '/pages/word_list_index/word_list_index'
							});
				        }
				    }
				});
			}else{
				this.getList()
			}
		}
		
		
		
		
	},
	onReachBottom(){
		console.log('已经到底')
		let _this = this
		uni.showModal({
		    title: '提示',
		    content: '是否开始下一页学习',
		    success: function (res) {
		        if (res.confirm) {
		            // console.log('用户点击确定');
					// _this.page++
					_this.getList()
		        } else if (res.cancel) {
		            // console.log('用户点击取消');
		        }
		    }
		});
	},
	onUnload(){
		console.log('onUnload')
		this.$store.dispatch('Word/page',this.page);
	},
	methods:{
		
		switch1Change: function (e) {
			// console.log('switch1 发生 change 事件，携带值为', e.target.value)
			this.translate_show = !this.translate_show
		},
		getList:function(){
			this.page++
			let data = {
				user_token:this.$store.getters.common_token,
				app_class:'wechat',
				which_dict:this.$store.state.Word.which_dict,
				read_model:this.$store.state.Word.read_model,
				read_order:this.$store.state.Word.read_order,
				page:this.page,
			}
			console.log(data)
			this.$api.word_list(data).then(res=>{
				console.log(res)
				this.content = this.content.concat(res.data.content) 
			})
		},
		goToDetail:function(){
			uni.navigateTo({
			    url: '/pages/word_list_detail/word_list_detail'
			});
		}
	}
}
